.dropdown {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    height: 100%;


    &__toggle {
        display: flex;
        flex-direction: row;
        gap: 7.5rem;
        width: calc(100% - 12rem);
        margin: auto;
        padding-bottom: 10px;
    }

    h4 {
        font-size: 18px;
        font-weight: 400;
        padding: 10px;
    }

    li {
        color: $color-1;
    }

    &__chevron {
        width: 15px;
        color: #FFFFFF;
        padding: 10px;
    }

    .active &__chevron {
        transform: rotate(180deg);
    }

    &__header {
        position: relative;
        @include flex-between;
        align-items: center;
        height: 3.25rem;
        background-color: #FF6060;
        border-radius: 5px;
        z-index: 10;
        cursor: pointer;
        width: 100%;
        color: #FFFFFF;

        &--aboutH {
            height: 52px;
        }
    }

    &__content {
        &--li {
            font-size: 18px;
            font-weight: 400;
            background: #F7F7F7;
            border-radius: 5px;
            color: #FF6060;
            margin-top: -10px;
            padding: 15px 15px 10px;
            z-index: 0;
        }

        &--li li {
            list-style: none;
        }
    }

    &__equipment {
        margin: 0;
        font-size: 12px;
        font-weight: 500;
        line-height: 142.6%;
        text-align: left;
        width: 100%;
    }


}


//*******/


@media (min-width: 1024px) and (max-width :1279.99px) {}

@media (min-width: 768px) and (max-width :1023.99px) {
    .dropdown {
        &__toggle {
            width: calc(100% - 6rem);
        }
    }
}

@media (min-width: 540px) and (max-width : 767.99px) {}

@media (min-width: 350px) and (max-width: 539.99px) {
    .dropdown {
        &__content {
            &--li {
                font-size: 12px;
                font-weight: 400;
            }

        }
    }
}