.tags {
    display: flex;
    padding: 1.25rem 5.8rem;
    white-space: nowrap;
    border-radius: 0.625rem;
    text-align: center;

    span {
        font-size: 0.875rem;
        padding: 0.313rem 1.563rem 0.313rem 1.563rem;
        margin-right: 0.625rem;
        border-radius: 0.625rem;
        color: white;
        font-size: 0.875rem;
        background-color: $color-1;
    }
}

.references {
   @include flex-between;
    flex-direction: row;
    width: calc(100% - 6.375rem);
    align-items: center;

img {
    gap:5px;
    width: 1.875rem;
    height: 1.875rem;
}
}


//*************  resposive *********

@media (min-width: 768px) and (max-width :1023.99px) {
    .tags{
        padding: 1.25rem 2.8rem;
    }
    .references {
        width: calc(100% - 3.375rem);
    }
}