.info {
    @include flex-between;
    flex-direction: row;
    margin-left: 6rem;
    margin-right: 6rem;
    align-items: center;

    &__left {
        font-size: 2.25rem;
        color: $color-1;
        margin-top: 34px;

        &--town {
            font-size: 1.125rem;

    
    }}

    .info__right {
        display: flex;
        align-items: center;
        margin-top: 15px;

        &--picture {
            border-radius: 50%;
            width: 4rem;
            height: 4rem;
        }

        &--name {
            padding-right: 0.625rem;
            color: $color-1;
            font-size: 1.125rem;
            font-weight: 500;
        }
    }
}

//********************  responsive  *****/

@media (min-width: 1280px) {

}

@media (min-width: 1024px) and (max-width :1279.99px) {
    .info {
        margin-left: 3rem;
        margin-right: 3rem;
        &__left {
            font-size: 2rem;
        }
    }
}

