.nav {
    width: calc(100% - 12rem);
    @include flex-between;
    align-items: flex-start;
    margin: auto;
    padding-bottom: 3.125rem;

    &__logo {
        margin-top: 15px;
    }

    ul {
        @include flex-end;
        margin: 1.875rem 2.5rem 0rem 0rem;
        align-items: flex-end;

        a {
            text-decoration: none;
            padding: 5px;
            width: 7.5rem;

            li {
                @include flex-end;
                font-size: 1.5rem;
                width: 7.5rem;
                font-weight: 500;
                list-style: none;
                color: $color-1;
                transition: 0.2s;
                cursor: pointer;

                &:hover {
                    color: $color-1;
                }
            }

            .nav__about {
                width: 9.125rem;   //? voir flo  en 350  :(
    
            }
        }

        //*barre en dessous de la nav
        .nav-active {
            text-decoration: underline;
            color: $color-1;

        }
    }
}

/***********   responsive ***********/


@media (min-width :1440px) {

}

@media (min-width: 768px) and (max-width :1023.99px) {}


@media (min-width: 540px) and (max-width : 767.99px) {
    .nav {
        width: calc(100% - 12rem);
        padding-bottom: 1.125rem;
    }

}

@media (min-width: 350px) and (max-width: 539.99px) {
    .nav {
        align-items: center;
        margin-left: 23px;
        font-size: 10px;
        padding-bottom: 1rem;


        &__logo {
            width: 80px;
            height: 30px;
    
        }

        &__home {
            margin-top: 18px;
            font-size: 50px;
        }

        ul>a>li {
            font-size: 14px;

        }
        ul {
            margin: inherit;
        }
      
    }}