.card {
    position: relative;
    width: 21.25rem;
    height: 21.25rem;
    border-radius: 0.625rem;
    &__picture {
        width: 100%;
        height: 100%;
         border-radius: 10px;
         object-fit: cover;
     }
 
    @media (min-width: 767.99px) and (max-width :1024px) {
        width: 16.25rem;
        height: 16.25rem;
    }
    @media (min-width: 350px) and (max-width :767px) {
        max-width: 25rem;
        max-height: 15rem;
    }

    &__title {
        position: absolute;
        margin: 0;
        left: 1.25rem;
        right: 1.25rem;
        bottom: 1.25rem;
        font-size: 1.125rem;
        font-weight: 500;
        text-align: left;
        color: white;
    }
}


