.banner {
    position: relative;
    @include flex-center;
    align-items: center;
    flex-shrink: 0;
    background-color: white;
    border-radius: 1.25rem;


    &__text {
        position: absolute;
        font-size: 1.875rem;
        color: white;
        font-weight: 500;

        @media (min-width: 374.99px) and (max-width :768px) {
            font-size: 0.875rem;
        }
    }

    &__image {
        width: calc(100% - 12.5rem);
        height: 13.938rem;
        object-fit: cover;
        -webkit-filter: brightness(20%);
        filter: brightness(90%);
        border-radius: 25px;
        margin-bottom: 10px;

    }
}

/*************************/

@media (min-width: 1024px) and (max-width :1279.99px) {}

@media (min-width: 768px) and (max-width :1023.99px) {}

@media (min-width: 540px) and (max-width : 767.99px) {

}

@media (min-width: 350px) and (max-width: 539.99px) {


        .banner{
            margin-left: 10px;
            margin-right: 10px;
            
        &__image{
            width: 100%;
            height: 111px;
            object-position: bottom 2px right -7px;
            margin-bottom: -25px;
            border-radius: 10px;
        }

        &__text {
            width: 217px;
            height:48px;
            left: 4%

        }
     
    }}
