//** @mixin  FLEXBOX

@mixin flex-between {
    display: flex;
    justify-content: space-between;
}

@mixin flex-end {
    display: flex;
    justify-content: flex-end;
}

@mixin flex-start {
    display: flex;
    justify-content: flex-start;
}

@mixin flex-center {
    display: flex;
    justify-content: center;
}

@mixin flex-column {
    display: flex;
    flex-direction: column;
}



